body {
    background-image: url(./assets/zont011.jpg);
}

* {
    box-sizing: border-box;

}

html {
    font-size: 18px;
}

body,
ul,
ol,
h1,
h2,
h3,
h4,
p {
    margin: 0;
    padding: 0;
}

ul,
ol {
    list-style: none;
}

a {
    text-decoration: none;
}

input,
button {
    outline: none;
}